/**
 * Section: Splash
 */


.splash {
  font-size: 16px;

  @media (min-width:@screen-sm) {
    font-size: 18px;
  }

    &__inner {
        padding-left: 40px;
        padding-right: 40px;
        text-align: center;
        margin-top: 20px;

        @media (min-width:@screen-sm) {
            width: 425px;
            position: absolute;
            left: 50%;
            top: 20%;
            text-align: left;
        }
    }

    &__title {
        // font-size: 32px;
        // font-weight: 300;
        display: block;
        position: relative;

        @media (min-width:@screen-sm) {.fadeIn(1.3s);}
    }

    &__information {
        // font-weight: 500;
        // font-size: 18px;
        position: relative;

        @media (min-width:@screen-sm) {.fadeIn(1.6s);}
    }

    &__link {
        color: @color-text;
        display: block;
        .fadeIn(2.5s);
    }

    &__bird {
        position: absolute;
        bottom: 5%;
        left: 50%;
        width: 70%;
        margin-left: -35%;
        max-width: 320px;
        .user-select(none);

        @media (min-width:600px) {
            margin-left: 0;
            left: 16%;
        }

        @media (max-height:580px) {display:none;}
        @media (min-width:@screen-sm) {left: 12%;}
        @media (min-width:@screen-md) {
            left: 8%;
            width: 30%;
            bottom: 15%;
            max-width: 100%;
        }
    }
}

.logo {
    text-decoration: none;
    color: @color-text;
    .clearfix();
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    width: 250px;
    .user-select(none);

    &:hover,
    &:focus {color: @color-text;}

    @media (min-width:@screen-sm) {
        width: 270px;
        left: -105px;
    }

    &__element {
        float: left;
        margin-right: 10px;
        position: relative;
        width: 170px;

        @media (min-width:@screen-sm) {
            width: 200px;
            .fadeIn(1.0s);
        }
    }

    &__text {
        float: left;
        padding-top: 23px;

        @media (min-width:@screen-sm) {
            padding-top: 29px;
            .fadeIn(.9s);
        }
    }

    &__type {
        clear:both;
        display: block;
        // font-weight: 300;
        font-size: 20px;
        padding-left: 55px;
        text-align: left;

        @media (min-width:@screen-sm) {
            font-size: 24px;
            padding-left: 66px;
            .fadeIn(1.2s);
        }


    }

    &__year {
        display: block;
        // font-weight: 500;
        font-size: 20px;
        line-height: 1;

        @media (min-width:@screen-sm) {
          font-size: 24px;
        }
    }
}
