/**
 * Base
 */

/* #box-sizing
********************/
* {
    .box-sizing(border-box);

    &:before,
    &:after {.box-sizing(border-box);}
}


/* #html
********************/
html {height: 100%;}


/* #body
********************/
body {
    #gradient > .diagonal(#fff; #b4cfbb; 20%;);
    font-family: @base-font-family;
    font-weight: @base-font-weight;
    font-size: @base-font-size;
    color: @color-text;
    line-height: 1.5;
    overflow: hidden;
    height: 100%;
    width: 100%;
}


/* #links
********************/
a {
    color: @color-link;
    text-decoration: underline;

    &:hover,
    &:focus {color: darken(@color-link, 10%);}
}


/* #image handling
********************/
figure {
    margin: 0;

    img {
        max-width: 100%;
        width: 100%;
        display: block;
    }
}


/* #icons
********************/
.icon {
    height: 24px;
    width: 24px;
    display: inline-block;
    fill: currentColor;
}
