/**
 * Base: Typography
 */

h1,
h2,
h3,
h4,
h5 {
    font-family: @header-font-family;
    font-weight: 300;
}

h1 {
    font-size: 21px;

    @media (min-width:@screen-sm) {font-size: 42px;}
}

h2 {
    font-size: 18px;

    @media (min-width:@screen-sm) {font-size: 38px;}
}

h3 {
    font-size: 16px;

    @media (min-width:@screen-sm) {font-size: 34px;}
}

p.lead {
    font-weight: bold;
    font-size: 14px;

    @media (min-width:@screen-sm) {font-size: 21px;}
}

bold,
strong {
    font-weight: 900;
}

p,
ul {margin-bottom: 30px;}
