/**
 * Component: Editor
 * This is not a editor per say, but stylings that have the purpose of making content look like its from an editor
 *
 * There are also mixins available, mostly for making content look like editor comments
 */


/* Line numbers
 * Note that lines are build with javascript
********************/
.line-numbers {
    position: absolute;
    top: 21px;
    left: 0;
    width: 25px;
    height: calc(~"100% - 21px");
    overflow: hidden;
    text-align: right;
    counter-reset: linenumbers;
    .user-select(none);

    &__line {
        &:before {
            display: block;
            counter-increment: linenumbers;
            content: counter(linenumbers);
            padding-right: 5px;
            color: @color-primary;
            opacity: .25;
        }
    }
}
